"use client";

import React, { useState, useEffect } from 'react';
import { usePrivateParkings } from '../hooks/Use_Service_EstacionamientoPrivado';

export default function PrivateParkingsPage() {
  const { parkings, loading, error, refresh } = usePrivateParkings();
  const [editingId, setEditingId] = useState(null);
  const [formData, setFormData] = useState({});
  const [message, setMessage] = useState('');

  const handleEdit = (parking) => {
    setEditingId(parking.id);
    // Deep copy to avoid mutating original list
    setFormData(JSON.parse(JSON.stringify(parking)));
    setMessage('');
  };

  const handleChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleTarifaChange = (index, campo, valor) => {
    const nuevasTarifas = [...formData.tarifas];
    nuevasTarifas[index][campo] = valor;
    setFormData({ ...formData, tarifas: nuevasTarifas });
  };


  const handleCancel = () => {
    setEditingId(null);
    setFormData({});
    setMessage('');
  };

  const handleAddTarifa = () => {
    const nuevaTarifa = {
      nombre: '',
      descripcion: '',
      precio: 0,
      moneda: 'USD' // o la moneda que uses por defecto
    };
    setFormData(prev => ({
      ...prev,
      tarifas: [...prev.tarifas, nuevaTarifa]
    }));
  };

  const handleSave = async () => {
    const { ok } = await modificar(formData);
    if (ok) {
      setMessage('¡Estacionamiento actualizado correctamente!');
      setEditingId(null);
    } else {
      setMessage('Falló la actualización.');
    }
  };

  if (loading) return <p className="text-center mt-10">Cargando...</p>;
  if (error) return <p className="text-center mt-10 text-red-600">{error}</p>;

  return (
    <div className="p-8 bg-gray-50 min-h-screen">
      <h1 className="text-2xl font-bold mb-6 text-center text-black">
        Estacionamientos Privados
      </h1>
      {message && <div className="mb-4 text-center text-green-700 font-medium">{message}</div>}
      <div className="overflow-x-auto bg-white shadow rounded-lg">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-100">
            <tr>
              <th className="px-6 py-3 text-left text-xl font-medium text-gray-500 uppercase text-black">Nombre</th>
              <th className="px-6 py-3 text-left text-xl font-medium text-gray-500 uppercase text-black">Dirección</th>
              <th className="px-6 py-3 text-left text-xl font-medium text-gray-500 uppercase text-black">Descripción</th>
              <th className="px-6 py-3 text-left text-xl font-medium text-gray-500 uppercase text-black">Estado</th>
              <th className="px-6 py-3 text-center text-xl font-medium text-gray-500 uppercase text-black">Acciones</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {parkings.map(p => (
              <React.Fragment key={p.id}>
                <tr className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap text-lg text-gray-800">{p.nombre}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-lg text-gray-600">{p.direccion}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-lg text-gray-600">{p.descripcion}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-lg">
                    <span className={`px-2 inline-flex text-lg leading-5 font-semibold rounded-full ${p.estado === 'Disponible' ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                      }`}>{p.estado}</span>
                  </td>
                  <td className="px-6 py-4 text-center">
                    <button
                      onClick={() => handleEdit(p)}
                      className="px-3 py-1 bg-blue-500 text-white rounded-lg hover:bg-blue-600"
                    >Modificar</button>
                  </td>
                </tr>

                {editingId === p.id && (
                  <tr>
                    <td colSpan={5} className="bg-gray-50 px-6 py-4">
                      <div className="bg-white p-4 rounded-lg shadow-md">
                        <p className="mb-2 text-gray-700">No altere la información del estacionamiento que no quiere cambiar.</p>
                        <div className="grid grid-cols-2 gap-4">
                          <input
                            type="text"
                            value={formData.nombre}
                            onChange={e => handleChange('nombre', e.target.value)}
                            placeholder="Nombre"
                            className="border p-2 rounded text-black"
                          />
                          <input
                            type="text"
                            value={formData.direccion}
                            onChange={e => handleChange('direccion', e.target.value)}
                            placeholder="Dirección"
                            className="border p-2 rounded text-black"
                          />
                          <input
                            type="number"
                            value={formData.latitud}
                            onChange={e => handleChange('latitud', parseFloat(e.target.value))}
                            placeholder="Latitud"
                            className="border p-2 rounded text-black"
                          />
                          <input
                            type="number"
                            value={formData.altitud}
                            onChange={e => handleChange('altitud', parseFloat(e.target.value))}
                            placeholder="Longitud"
                            className="border p-2 rounded text-black"
                          />
                          <input
                            type="number"
                            value={formData.capacidad}
                            onChange={e => handleChange('capacidad', parseInt(e.target.value, 10))}
                            placeholder="Capacidad"
                            className="border p-2 rounded text-black"
                          />
                          <input
                            type="text"
                            value={formData.descripcion}
                            onChange={e => handleChange('descripcion', e.target.value)}
                            placeholder="Descripción"
                            className="border p-2 rounded col-span-2 text-black"
                          />
                        </div>
                        <div className="mt-4">
                          <p className="font-medium text-black">Tarifas:</p>
                          {formData.tarifas.map((t, idx) => (
                            <div key={idx} className="grid grid-cols-2 md:grid-cols-4 gap-2 mt-2">
                              <input
                                type="text"
                                value={t.nombre}
                                onChange={e => handleTarifaChange(idx, 'nombre', e.target.value)}
                                placeholder="Nombre"
                                className="border p-2 rounded text-black"
                              />
                              <input
                                type="text"
                                value={t.descripcion}
                                onChange={e => handleTarifaChange(idx, 'descripcion', e.target.value)}
                                placeholder="Descripción"
                                className="border p-2 rounded text-black"
                              />
                              <input
                                type="number"
                                value={t.precio}
                                onChange={e => handleTarifaChange(idx, 'precio', parseFloat(e.target.value))}
                                placeholder="Precio"
                                className="border p-2 rounded text-black"
                              />
                              <input
                                type="text"
                                value={t.moneda}
                                onChange={e => handleTarifaChange(idx, 'moneda', e.target.value)}
                                placeholder="Moneda"
                                className="border p-2 rounded text-black"
                              />
                            </div>
                          ))}

                          <button
                            onClick={handleAddTarifa}
                            className="mt-2 px-3 py-1 bg-indigo-500 text-white rounded hover:bg-indigo-600"
                          >
                            + Añadir tarifa
                          </button>
                        </div>
                        <div className="mt-4 flex justify-end gap-2">
                          <button
                            onClick={handleCancel}
                            className="px-4 py-2 bg-gray-300 text-gray-800 rounded-lg hover:bg-gray-400"
                          >Cancelar</button>
                          <button
                            onClick={handleSave}
                            className="px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600"
                          >Guardar</button>
                        </div>
                      </div>
                    </td>
                  </tr>
                )}
              </React.Fragment>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
